.site-list {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-areas: "about work . shop contact";
  grid-gap: 20px;
  align-content: center; }

.site-list__item {
  justify-self: center; }
  .site-list__item--about-us {
    grid-area: about; }
  .site-list__item--work {
    grid-area: work; }
  .site-list__item--shop {
    grid-area: shop; }
  .site-list__item--contact {
    grid-area: contact; }

.site-list__link {
  color: #444444;
  text-transform: uppercase;
  font-weight: 700;
  transition: .3s all; }
  .site-list__link:hover {
    color: #64c29e; }
  .site-list__link:active {
    transform: scale(0.95); }

@media (max-width: 568px) {
  .site-list {
    grid-template-columns: repeat(3, minmax(35px, 1fr));
    grid-template-rows: repeat(2, auto);
    grid-template-areas: "about . contact" "work . shop"; } }

.about-us {
  background-color: #f7f7f7;
  display: grid;
  grid-template-rows: minmax(50px, auto) minmax(230px, auto) minmax(50px, auto);
  align-items: center;
