  grid-area: span 2 / span 1; }

.work__bike--2 .work__pic--L {
  grid-column: 2 / 4;
  grid-row: 1 / 4; }

.work__bike--2 .work__logo {
  margin-top: -75px;
  margin-left: -90px; }

.work__bike--2 .work__pic--S {
  grid-area: span 1 / span 1;
  order: 1; }

.work__bike--2 .work__content {
  grid-area: span 2 / span 1; }

.work__bike--3 .work__pic--M {
  grid-column: 1 / 2;
  grid-row: 1 / 4; }

.work__bike--3 .work__pic--M-2 {
  grid-column: 3 / 4;
  grid-row: 1 / 4; }

.work__bike--3 .work__logo {
  margin-top: -32px;
  margin-left: -100px; }

.work__bike--3 .work__pic--S {
  grid-area: span 1 / span 1;
  order: 1; }

.work__bike--3 .work__content {
  grid-area: span 2 / span 1; }

@media (max-width: 1024px) {
  .work__wrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center; }
  .work__title {
    font-size: 17px;
    line-height: 32px;
    margin-right: 20px;
    order: -1; }
  .work__subtitle {
    font-size: 15px; } }

@media (max-width: 768px) {
  .work__bike {
    max-height: 800px;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: minmax(250px, auto) minmax(400px, auto); }
  .work__content {
    max-width: 100%; }
  .work__pic--S {
    display: none; }
  .work__bike--1 .work__pic--L,
  .work__bike--2 .work__pic--L {
    grid-column: 1 / 3;
    grid-row: 1 / 2;
    min-height: 200px; }
  .work__bike--1 .work__content,
  .work__bike--2 .work__content {
    grid-area: span 1 / span 2; }
  .work__bike--3 {
    position: relative; }
    .work__bike--3 .work__pic--M {
      grid-column: 1 / 2;
      grid-row: 1 / 2; }
    .work__bike--3 .work__pic--M-2 {
      grid-column: 2 / 3;
      grid-row: 1 / 2; }
    .work__bike--3 .work__logo {
      display: none; }
    .work__bike--3 .work__logo--2 {
      display: grid;
      background-color: transparent;
      justify-self: center;
      align-self: center;
      height: auto;
      opacity: 1;
      z-index: 1;
      position: absolute;
      top: 20%;
      margin-left: -100px;
      left: 50%; }
    .work__bike--3 .work__content {
      grid-area: span 1 / span 2; } }

@media (max-width: 568px) {
  .work__bike--3 .work__logo--2 {
    top: 15%; } }

.twitter {
  background-color: #f7f7f7;
  color: #848789;
