  display: grid;
  justify-items: center;
  align-items: center;
  max-width: 80%; }

.twitter__blockquote {
  display: none;
  text-align: center;
  height: 175px; }
  .twitter__blockquote--1 {
    display: block; }

.twitter__cite {
  font-weight: 700;
  font-style: normal;
  color: #444444; }

.twitter__cite span {
  color: #848789;
